  font-family: inherit;
  font-size: 10px;
  font-weight: 500;
  line-height: 1.16;
  box-shadow: 0 0 62px 2px #00000054;
}

.theme-switch__control:after {
  content: "LIGHT";
  text-transform: uppercase;
  color: #fff;
  font-family: inherit;
  font-size: 10px;
  font-weight: 500;
  line-height: 1.16;
  box-shadow: 0 0 62px 2px #00000054;
}

