  }

  .theme-switch__control:before, .theme-switch__control:after {
    font-size: 12px;
    font-weight: 500;
    line-height: 1.16;
  }
}

.theme-switch__track {
  width: 30px;
  height: 30px;
  cursor: pointer;
  background-color: #ff6b08;
  border-radius: 50px;
  margin: 0 10px;
  display: inline-block;
  position: relative;
  box-shadow: inset 0 0 2px 1px #0000001a, 0 0 10px 1px #ef4247;
}

.theme-switch__track:before {
  content: "";
  height: 20px;
  width: 20px;
  background-color: #ff6b08;
  border-radius: 50%;
  transition: width .2s cubic-bezier(0, -1.85, .27, 1.75);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: inset 0 0 0 3px #fff;
}

.theme-switch__toggle {
  display: none;
  position: relative;
}

.theme-switch__toggle:checked + .theme-switch__track {
  background-color: #57de72;
  box-shadow: inset 0 0 2px 1px #0000001a, 0 0 10px 6px #03841c8a;
}

.theme-switch__toggle:checked + .theme-switch__track:before {
  width: 2px;
  background-color: #fff;
}

