.tui-pagination .tui-ico-ellip,
.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-last {
  width: 16px;
  height: 16px;
  fill: #ff6b08;
  transition: all 250ms ease-in-out;
}

.section-pagination {
  margin-top: 40px;
  margin-bottom: 40px;
}

.tui-pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.tui-page-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;

  font-family: 'Roboto';
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  background: transparent;
  border-radius: 5px;
  border: 0;
  color: var(--second-color);
  &:hover,
  &:focus {
    background-color: #ff6b08;
    color: #ffffff;
  }
  transition: all 250ms ease-in-out;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #ffffff;
  background: #ff6b08;
  cursor: default;
}

.tui-prev,
.tui-next {
  background-color: transparent;
  &:hover,
  &:focus {
    background-color: rgba(0, 0, 0, 0.3);
  }
}

.tui-first,
.tui-last {
  display: none;
}

.tui-pagination .tui-is-disabled:hover {
  background-color: transparent;
  cursor: default;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 11px;
  height: 2px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-pagination .tui-ico-ellip {
  width: 11px;
  height: 2px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}
